<script>
  import { getContext } from "svelte";
  import { json, openField } from "./stores.js";

  export let key;
  export let open = false;

  $: open = $openField === key;

  function getLabel() {
    return key.replace("_", " ");
  }

  function handleChange(key) {
    $openField = key;
  }
</script>

<style type="text/scss">.truncate {
  display: block;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
  margin-bottom: 0;
  padding-left: 0; }

.close {
  padding-left: 0;
  margin-bottom: 0; }

/*# sourceMappingURL=EditableTextArea.svelte.css.map */</style>

{#if !open}
  <button
    class="truncate button-primary-text"
    on:click={() => handleChange(key)}>
    <label>{getLabel()}</label>
    : {$json[key]}
  </button>
{:else}
  <fieldset>
    <button
      class="truncate button-primary-text"
      on:click={() => ($openField = null)}>
      <label for={`input_text_${key}`}>{key}</label>
    </button>
    <textarea id={`input_text_${key}`} name={key} class="" rows={12} bind:value={$json[key]}></textarea>
    <button
      class="close button-primary-text"
      on:click={() => ($openField = null)}>
      Close
    </button>
  </fieldset>
{/if}
